import { useEffect, useState } from "react";
import "./styles.css";

const types = [
  { name: "Robot", value: 1 },
  { name: "CyberMan", value: 2 },
  { name: "Robot Head", value: 3 },
  { name: "Cat", value: 4 }
];

export default function App() {
  const [robohash, setRobohash] = useState("");
  const [currType, setCurrType] = useState(0);
  const [imageURL, setImageURL] = useState("");
  const url = `https://robohash.org/${robohash}.png?set=set${currType}`;

  useEffect(() => {
    window.addEventListener("keypress", (e) => {
      if (e.which === 13 && currType) {
        setImageURL(`https://robohash.org/${robohash}.png?set=set${currType}`);
      }
    });
  }, [robohash, currType, imageURL]);

  const printPicture = (e) => {
    setRobohash(e.target.value);
  };

  function onOptionChange(e) {
    setCurrType(e.target.value);
    if (e.target.value === currType) {
      setImageURL(url);
    }
  }

  function onGenerate(url) {
    if (currType && robohash) {
      setImageURL(url);
    }
  }

  return (
    <div className="App">
      <h1>RoboHash</h1>
      <h2>GENERATE UNIQUE IMAGES FROM ANY TEXT</h2>
      <div className="form">
        <input
          type="text"
          onChange={(e) => printPicture(e)}
          placeholder="Enter any text here... âœŽ"
        />

        <select value={currType} onChange={onOptionChange}>
          <option value={0} disabled>
            Select a Type ðŸ‘ˆ
          </option>
          {types.map((type) => {
            return (
              <option key={type.value} value={type.value}>
                {type.name}
              </option>
            );
          })}
        </select>

        <button onClick={() => onGenerate(url)}>
          Generate{" "}
          <span aria-label="robot" role="img">
            ðŸ¤–
          </span>
        </button>
      </div>
      {imageURL ? (
        <>
          <img src={imageURL} alt={robohash} />
          <div className="background"></div>
        </>
      ) : (
        <p></p>
      )}
      {/* <p>{imageURL ? `${types[currType - 1]?.name} ${robohash}` : ""}</p> */}
      <footer>
        Robots lovingly{" "}
        <span aria-label="heart" role="img">
          ðŸ–¤
        </span>{" "}
        delivered by <a href="https://robohash.org">Robohash.org</a>.
        <br /> Created by{" "}
        <a href="https://github.com/skotancilar">kotancilar</a> .
      </footer>
    </div>
  );
}
